
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'

import {httpUrl} from '../../http_url';

export default {
name: 'HelloWorld',
data () {
return {
show: true,
phone: '',
identifyCode:'',
time:60,
start: false
}
},
components: {
Group,
Cell,
XInput,
XButton, Alert,Countdown
},
methods: {
getCode(){
console.log(this.SpaceTransformation(this.phone));
let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
type:100
})
this.$axios({
method: 'post',
url:httpUrl.getCode,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$vux.toast.show({
text: '验证码发送成功'
});
this.show = false;
this.start = true;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
finish (index) {
this.start = false;
this.time = 60;
this.show = true;
},
goLogin(){
this.$router.push('/login');
},
loginSubmit(){
const phone = this.SpaceTransformation(this.phone);

let postData = this.$qs.stringify({
phone:this.SpaceTransformation(this.phone),
code:this.identifyCode
})

if(this.isEmpty(phone)){
this.$vux.toast.show({
type:'warn',
text: '请输入手机号'
})
return
}else if(!this.Val.isPhoneNum(phone)){
this.$vux.toast.show({
type:'warn',
text: '请输入正确手机号'
})
return
}else if(this.isEmpty(this.identifyCode)){
this.$vux.toast.show({
type:'warn',
text: '请输入验证码'
})
return
}else {
this.$axios({
method: 'post',
url:httpUrl.codeLogin,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
data:postData
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/home');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
}


}
},
filters: {

}
}



